html,
body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
  overflow-y: auto;
  background-color: #eee;
}
.container,
.swiper {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0px;
  top: 0px;
  background: black;
  overflow: hidden;
}
.container {
  display: flex;
  justify-content: center;
  align-items: center;
}
#console {
  position: fixed;
  width: 100%;
  height: 15%;
  left: 0px;
  top: 0;
  text-align: center;
  overflow: auto;
  color: #fff;
  border-bottom: 1px solid rgba(255, 255, 255, 0.25);
  overflow-y: auto;
}
#btn {
  position: fixed;
  width: 100%;
  height: 8%;
  left: 0px;
  top: 10%;
  text-align: center;
}
.imglist {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  .img {
    width: 270px;
    height: 180px;
    margin: 12px;
    -o-object-fit: cover;
    object-fit: cover;
  }
}
